<template>
  <div class="component-home" :browser="browser" :console="visible ? 'visible' : 'hidden'">
    <nav>
      <a href="/" class="brand font-parisienne">P</a>
      <ul>
        <li v-for="(n, idx) in state.navs" :class="{ active: n.active }" :key="idx" v-show="n.visible">
          <router-link :to="'/' + n.name">{{ n.title }}</router-link>
        </li>
      </ul>
      <div class="right">
        <span class="lang">
          <div :title="store.state.lang === 'ko' ? '언어(Lnaguage)' : 'Language(언어)'" :style="{ backgroundColor: store.state.lang === 'ko' ? '#0047a0' : '#012169' }" @click="setLang()">
            <span
              class="toggle"
              :style="{
                backgroundColor: store.state.lang === 'ko' ? '#fff' : '#012169',
                backgroundImage: store.state.lang === 'ko' ? 'url(/img/logo.ko.svg)' : 'url(/img/logo.en.svg)',
                left: store.state.lang === 'ko' ? '' : 'calc(100% - 32px)',
              }"
            />
            <span class="text" :style="{ right: store.state.lang === 'ko' ? '' : 'calc(100% - 26px)' }">{{ store.state.lang }}</span>
          </div>
          <img src="/img/logo.en.svg" class="hide" />
        </span>
        <button class="btn" @click="callback('toggle-console')">
          <template v-if="store.state.lang === 'ko'">
            <span>콘솔 </span>
            <span v-if="visible">닫기</span>
            <span v-else>열기</span>
          </template>
          <template v-else>
            <span v-if="visible">CLOSE </span>
            <span v-else>OPEN </span>
            <span>CONSOLE</span>
          </template>
        </button>
      </div>
    </nav>
    <article>
      <router-view :visible="visible" :callback="callback" :load="setLnbEvents" />
    </article>
    <footer>
      <div class="wrapper">
        <div class="copyright">&copy; 2021. Publessing. All rights reserved.</div>
        <router-link to="/community" class="email">africalibrary21@gmail.com</router-link>
      </div>
    </footer>
    <button class="climb btn color-point border-point" ref="climbBtnRef" @click="climb()">TOP</button>
  </div>
</template>
<script>
import { onMounted, computed, reactive, ref } from "@vue/runtime-core";
import commLib from "../libs/commonLib";
import defines from "../defines";
import $ from "jquery";
import { useStore } from "vuex";

export default {
  props: {
    visible: Boolean,
    callback: Function,
  },
  setup() {
    const store = useStore();
    const state = reactive({
      nav: "intro",
      navs: computed(() => {
        return [
          { name: "intro", title: store.state.lang === "ko" ? "퍼블레싱" : "Intro", active: state.nav === "intro", visible: true },
          { name: "learn", title: store.state.lang === "ko" ? "시작하기" : "Learn", active: state.nav === "learn", visible: true },
          { name: "license", title: store.state.lang === "ko" ? "라이선스" : "License", active: state.nav === "license", visible: true },
          { name: "community", title: store.state.lang === "ko" ? "커뮤니티" : "Community", active: state.nav === "community", visible: true },
        ];
      }),
    });

    let timer = null;
    const climbBtnRef = ref(null);
    const browser = commLib.getBrowser();

    const setLang = () => {
      const lang = store.state.lang === "ko" ? "en" : "ko";
      sessionStorage.setItem("lang", lang);

      if (lang === "ko") {
        commLib.message.show("success", "언어를 변경하였습니다.");
      } else {
        commLib.message.show("success", "The language has changed.");
      }

      defines.site.lang = lang;
      store.commit("setLang", lang);
    };

    const climb = () => {
      $("html,body").animate({
        scrollTop: 0,
      });
    };

    const check = () => {
      if (window.scrollY > 300) {
        $(climbBtnRef.value).fadeIn();
      } else {
        $(climbBtnRef.value).fadeOut();
      }
    };

    const activate = () => {
      if (location.pathname && location.pathname.length > 1) {
        state.nav = location.pathname.substring(1);
      }
    };

    const setLnbEvents = (lnb) => {
      lnb.addEventListener("click", (e) => {
        if (e.target.tagName.toLowerCase() === "li") {
          const children = [...e.target.parentElement.children];
          const idx = children.indexOf(e.target);
          const content = lnb.parentElement.querySelector(".content");
          const scrollTop = (content.querySelectorAll(".sm-title")[idx].offsetTop || 0) - 80;

          children.forEach((c, i) => {
            if (i === idx) {
              c.classList.add("color-point");
              c.classList.add("border-point");
            } else {
              c.classList.remove("color-point");
              c.classList.remove("border-point");
            }
          });

          $("html, body")
            .stop()
            .animate({ scrollTop });
        }
      });
    };

    window.onscroll = () => {
      clearTimeout(timer);

      timer = setTimeout(() => {
        check();
      });
    };

    onMounted(() => {
      check();
    });

    return { store, state, setLang, climbBtnRef, browser, climb, activate, setLnbEvents, defines };
  },
  watch: {
    $route() {
      $(window).scrollTop(0);
      this.activate();
    },
  },
};
</script>
<style lang="scss">
.component-home {
  position: relative;
  min-width: 991px;

  .video {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    border: 1px solid #ddd;

    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  .wrapper {
    width: 1140px;
    margin: 0 auto;
    max-width: 100%;
    padding: 0 15px;
  }

  > nav {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #ddd;
    background: #f7f7f7;
    transition: background-color 0.25s;
    padding-left: 70px;
    padding-right: 170px;
    z-index: 1;

    .brand {
      position: absolute;
      color: #000;
      top: 0;
      left: 0;
      font-size: 20px;
      cursor: pointer;
      font-weight: bold;
      text-decoration: none;
      border-right: 1px solid #ddd;
      height: 100%;
      width: 60px;
      text-align: center;
      line-height: 64px;
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      li {
        display: inline-block;
        margin: 0 5px;

        > a {
          color: inherit;
          padding: 19px 10px;
          cursor: pointer;
          display: inline-block;
          font-size: 14px;
          text-decoration: none;
        }

        &.active {
          font-weight: bold;
        }
      }
    }

    .right {
      position: absolute;
      top: 6.5px;
      right: 7px;

      .lang {
        padding-top: 7px;
        padding-right: 13px;
        display: inline-block;
        vertical-align: top;
        color: #fff;

        > div {
          display: inline-block;
          width: 66px;
          height: 32px;
          margin: 0 5px;
          border: 1px solid #ccc;
          border-radius: 16px;
          cursor: pointer;
          background: #363636;
          position: relative;
          transition: background-color 0.25s;

          .toggle {
            display: inline-block;
            background-size: 100%;
            background-position: left center;
            background-color: #fff;
            background-repeat: no-repeat;
            border-radius: 50%;
            border: 1px solid #aaa;
            width: 32px;
            height: 32px;
            position: absolute;
            top: -1px;
            left: -1px;
            transition: background-color 0.25s, background-image 0.25s, left 0.25s;
          }

          .text {
            color: #fff;
            font-size: 13px;
            position: absolute;
            right: 11px;
            top: 4px;
            transition: right 0.25s;
          }
        }
      }

      button {
        border-radius: 0;
        box-shadow: none;
        border: 1px solid #146ece;
        background: inherit;
        color: #146ece;
        padding: 11.5px 0;
        width: 140px;
        font-size: 14px;

        &:hover {
          background: #146ece;
          color: #fff;
        }
      }

      &.white {
        background: #fff;
      }
    }
  }

  > article {
    .parallax {
      padding-top: 100px;
      padding-bottom: 100px;
      color: #fff;
      text-align: center;
      position: relative;

      h1 {
        font-size: 42px;
        letter-spacing: 1px;
        margin-bottom: 20px;
        font-weight: bold;
      }

      h2 {
        font-size: 21px;
        font-weight: 100;
      }

      &:before {
        content: "";
        display: block;
        background-size: cover;
        background-position: center;
        background-attachment: fixed;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        filter: brightness(0.3);
        z-index: -1;
      }
    }

    .lnb {
      float: left;
      width: 21%;
      padding-top: 10px;
      padding-right: 50px;
      position: sticky;
      top: 55px;

      ul {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
          padding: 12.5px 15px;
          margin: 5px 0;
          border: 1px solid #ddd;
          cursor: pointer;
          font-size: 14px;
        }
      }
    }

    .content {
      float: left;
      width: 79%;

      .part {
        padding-bottom: 25px;

        .sm-title {
          font-size: 18px;
          font-weight: 600;
          padding-bottom: 15px;
          margin-top: 25px;
          border-bottom: 1px solid #ccc;
          margin-bottom: 15px;
        }
      }
    }
  }

  > footer {
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    padding: 35px 0;
    background: #f7f7f7;

    > .wrapper:after {
      content: "";
      clear: both;
      display: table;
    }

    .copyright {
      float: left;
    }

    .email {
      color: inherit;
      float: right;
    }
  }

  > .climb {
    position: fixed;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 0;
    right: 15px;
    bottom: 15px;
    padding: 0;
    width: 66px;
    height: 66px;
    line-height: 66px;
    text-align: center;
    border: 1px solid;
    box-shadow: none;
    display: none;
    z-index: 10;

    &.on {
      opacity: 1;
    }
  }

  &[browser="safari"] {
    > article > div.parallax:before {
      background-attachment: scroll;
    }
  }

  @media screen and (orientation: landscape) {
    &[device="mobile"] > header {
      margin-top: 0;
    }
  }
}
</style>
